import {DefaultRelays} from "../configs/Constants";

const getItem = (key) => {
    return localStorage.getItem(key);
};

const setItem = (key, value) => {
    localStorage.setItem(key, JSON.stringify(value));
};

const removeItem = (key) => {
    localStorage.removeItem(key);
};

export const getKeys = () => {
    try {
        return JSON.parse(getItem("keys"))
    } catch (e) {
        return null;
    }
};
export const storeKeys = (keys) => setItem("keys", keys);
export const removeKeys = () => removeItem("keys");

export const getRelays = () => getItem("relays") || DefaultRelays;