/* eslint-disable react-hooks/exhaustive-deps */
import {useEffect} from "react";
import {useAtom} from "jotai";
import _ from "lodash";
import {initNostr} from "../nostr/Nostr";
import {
    isConnectedAtom,
    keysAtom,
    ntrAtom,
    ntrStatusAtom,
    typingsAtom
} from "../configs/Atom";
import {NostrEventsEnum} from "../configs/Constants";

const NostrProvider = (props) => {
    const [keys] = useAtom(keysAtom);
    const [ntr, setNtr] = useAtom(ntrAtom);
    const [ntrStatus, setNtrStatus] = useAtom(ntrStatusAtom);
    const [typings, setTypings] = useAtom(typingsAtom);
    const [, setIsConnected] = useAtom(isConnectedAtom);

    const handleReadyState = () => {
        console.info("handleReadyState");
        setIsConnected(true);
        setNtrStatus({
            ...ntrStatus,
            ready: true
        });
    };

    const handleDmsDoneState = () => {
        console.info("handleDmsDoneState");
        setIsConnected(true);
        setNtrStatus({
            ...ntrStatus,
            dmsDone: true
        });
    };

    const handleSyncDoneState = () => {
        console.info("handleSyncDoneState");
        setIsConnected(true);
        setNtrStatus({
            ...ntrStatus,
            syncDone: true
        });
    };

    const handleTyping = (data) => {
        console.info("handleTyping", data);
        setIsConnected(true);
        setTypings(_.uniqBy([
            ...data,
            ...typings
        ], "peer"));
    };

    /* Init Nostr */
    useEffect(() => {
        setNtr(initNostr(keys));

        return () => {
            ntr?.removeListener(NostrEventsEnum.Ready, handleReadyState);
            ntr?.removeListener(NostrEventsEnum.Typing, handleTyping);

            setNtr(null);
        };
    }, [keys]);

    useEffect(() => {
        ntr?.removeListener(NostrEventsEnum.Ready, handleReadyState);
        ntr?.addListener(NostrEventsEnum.Ready, handleReadyState);

        return () => {
            ntr?.removeListener(NostrEventsEnum.Ready, handleReadyState);
        };
    }, [ntrStatus, ntr]);

    useEffect(() => {
        ntr?.removeListener(NostrEventsEnum.DMsDone, handleDmsDoneState);
        ntr?.addListener(NostrEventsEnum.DMsDone, handleDmsDoneState);

        return () => {
            ntr?.removeListener(NostrEventsEnum.DMsDone, handleDmsDoneState);
        };
    }, [ntrStatus, ntr]);

    useEffect(() => {
        ntr?.removeListener(NostrEventsEnum.SyncDone, handleSyncDoneState);
        ntr?.addListener(NostrEventsEnum.SyncDone, handleSyncDoneState);

        return () => {
            ntr?.removeListener(NostrEventsEnum.SyncDone, handleSyncDoneState);
        };
    }, [ntrStatus, ntr]);

    useEffect(() => {
        ntr?.removeListener(NostrEventsEnum.Typing, handleTyping);
        ntr?.addListener(NostrEventsEnum.Typing, handleTyping);

        return () => {
            ntr?.removeListener(NostrEventsEnum.Typing, handleTyping);
        };
    }, [ntr, typings]);

    return (
        <>{props.children}</>
    );
};

export default NostrProvider;