import {Button, Image} from "antd";
import {StarFill} from "../images/Icons";
import placeholder from "../images/placeholder.png";
import "../styles/components/AnimeItem.css";

const AnimeItem = (props) => {
    const toggle = () => {
        const elements = document.getElementsByClassName("app-anime-item");
        const element = document.getElementById(`app-anime-item-${props.anime.id}`);
        for (const el of elements) {
            if (el.id !== element.id && el.firstElementChild.classList.contains("open")) el.firstElementChild.classList.toggle("open");
        }
        element.firstElementChild.classList.toggle("open");
    };

    return (
        <div id={`app-anime-item-${props.anime.id}`} className="app-anime-item mt-4" onClick={toggle}>
            <div className="app-anime-item-container">
                <Image
                    src={props.anime.image}
                    fallback={placeholder}
                    alt={"Anime"}
                    preview={false}
                    className="app-anime-item-image"
                />
                <div className="app-anime-item-content">
                    <div className="d-flex align-items-center">
                        <p className="m-0 app-text-color">{props.anime.name}</p>
                        <div className="app-background-color rounded px-2 py-1 ms-2">
                            <p className="m-0 app-text-color fs-x-small"><StarFill width={12} height={12} color="var(--app-primary-progress-color)" /> {props.anime.rating}</p>
                        </div>
                    </div>
                    <p className="m-0 app-text-color-tertiary small">{props.anime.genres.map(genre => genre.name).join(", ")}</p>
                    <div className="app-anime-item-footer">
                        <p className="mt-2 mb-0 app-text-color-secondary small">{props.anime.overview}</p>
                    </div>
                </div>
                <div className="">
                    <p className="m-0 app-text-color app-anime-item-episode">{props.anime.episodes.length}/{props.anime.episodes_count}</p>
                    <div className="app-anime-item-footer">
                        <Button
                            type="primary"
                            shape="round"
                            className="mt-3"
                        >
                            Tonton
                        </Button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default AnimeItem;