/* eslint-disable react-hooks/exhaustive-deps */
import {useEffect} from "react";
import {useAtom} from "jotai";
import {isLoginAtom, keysAtom} from "../configs/Atom";
import {DefaultEmptyValue} from "../configs/Constants";
import {getKeys} from "../storage/LocalStorage";
import IsNotEmpty from "../helpers/IsNotEmpty";

const KeysProvider = (props) => {
    const [keys, setKeys] = useAtom(keysAtom);
    const [, setIsLogin] = useAtom(isLoginAtom);

    useEffect(() => {
        setKeys(getKeys());
    }, []);

    if (keys === DefaultEmptyValue) {
        return null;
    } else {
        if (IsNotEmpty(keys)) setIsLogin(true);
    }

    return (
        <>{props.children}</>
    );
};

export default KeysProvider;