/* eslint-disable react-hooks/exhaustive-deps */
import {useEffect} from "react";
import {useAtom} from "jotai";
import {unixIntervalAtom} from "../configs/Atom";
import UnixTime from "../helpers/UnixTime";

const UnixProvider = (props) => {
    const [unixInterval, setUnixInterval] = useAtom(unixIntervalAtom);

    useEffect(() => {
        const interval = setInterval(() => {
            setUnixInterval(UnixTime());
        }, 1000);

        return () => {
            clearInterval(interval);
        };
    }, [unixInterval]);

    return (
        <>{props.children}</>
    );
};

export default UnixProvider;