import * as Comlink from "comlink";
import {
    getEventHash,
    getPublicKey,
    getSignature,
    Kind,
    nip04,
    nip19,
    SimplePool
} from "nostr-tools";
import _ from "lodash";
import {NostrEvents} from "./NostrEvents";
import {getRelays} from "../storage/LocalStorage";
import IsEmpty from "../helpers/IsEmpty";
import IsNotEmpty from "../helpers/IsNotEmpty";
import IsSha256 from "../helpers/IsSha256";
import UnixTime from "../helpers/UnixTime";
import {
    CustomKinds,
    GlobalChat,
    IsNip07,
    MessagePerPage,
    NostrEventsEnum, NostrTagsEnum
} from "../configs/Constants";

export default class Nostr extends NostrEvents {
    constructor(privateKey, publicKey) {
        super();

        this.worker = new Worker(new URL("Worker.js", import.meta.url));
        this.backgroundNostr = Comlink.wrap(this.worker);
        this.privateKey = privateKey;
        this.nsec = nip19.nsecEncode(this.privateKey);
        this.publicKey = IsEmpty(this.privateKey) || privateKey === IsNip07 ? publicKey : getPublicKey(this.privateKey);
        this.npub = nip19.npubEncode(this.publicKey);
        this.relays = getRelays();
        this.readRelays = Object.keys(this.relays).filter(value => this.relays[value].read);
        this.writeRelays = Object.keys(this.relays).filter(value => this.relays[value].write);
        this.eventQueue = [];
        this.eventQueueBuffer = [];
        this.eventQueueFlag = true;
        this.eventQueueTimer = null;
        this.nameCache = {};
        this.listenerSub = null;
        this.messageListenerSub = null;

        this.backgroundNostr.setRelays(this.readRelays);

        if (IsNotEmpty(this.privateKey) && IsNotEmpty(this.publicKey)) this.init().then();
    }

    async init() {
        const events = await this.getEventCreated();
        await this.getIncomingPrivateMessages();
        await this.getChannelMessages(events);
    }

    async getEventCreated() {
        const events = await this.fetch([{
            authors: [this.publicKey]
        }]);

        events.filter(event => event.kind !== Kind.ChannelMessage)
            .forEach(event => this.pushToEventBuffer(event));

        this.emit(NostrEventsEnum.Ready);

        return events;
    }

    async getIncomingPrivateMessages() {
        const incomingPrivateMessages = await this.fetch([{
            kinds: [Kind.EncryptedDirectMessage],
            "#p": [this.publicKey]
        }]);

        incomingPrivateMessages.forEach(event => this.pushToEventBuffer(event));

        this.emit(NostrEventsEnum.DMsDone);
    }

    async getChannelMessages(events) {
        const deletions = events.filter(event => event.kind === Kind.EventDeletion)
            .map(event => Nostr.findTagValue(event, "e"))
            .filter(IsNotEmpty);

        const channelIds = _.uniq(events.map(event => {
            if (event.kind === Kind.ChannelCreation) return event.id;
            if (event.kind === Kind.ChannelMessage) return Nostr.findTagValue(event, "e");

            return null;
        }))
            .filter(IsNotEmpty)
            .filter(event => !deletions.includes(event))
            .filter(IsNotEmpty)

        if (!channelIds.includes(GlobalChat.id)) channelIds.push(GlobalChat.id);

        const channels = await this.fetch([
            ..._.chunk(channelIds, 10).map(channelId => ({
                kinds: [Kind.ChannelCreation],
                ids: channelId
            }))
        ]);

        channels.forEach(event => this.pushToEventBuffer(event));

        const filters = channels.map(event => event.id)
            .map(eventId => ([
                {
                    kinds: [Kind.ChannelMetadata, Kind.EventDeletion],
                    "#e": [eventId]
                },
                {
                    kinds: [Kind.ChannelMessage],
                    "#e": [eventId],
                    limit: MessagePerPage
                }
            ]))
            .flat();

        const promises = _.chunk(filters, 6).map(filter => {
            return this.fetch(filter)
                .then(events => {
                    events.forEach(event => this.pushToEventBuffer(event))
                });
        });

        await Promise.all(promises);

        this.emit(NostrEventsEnum.SyncDone);
    }

    static parseJson(data) {
        try {
            return JSON.parse(data);
        } catch (e) {
            return null;
        }
    }

    static normalizeMetadata(meta) {
        return {
            name: meta.name || "",
            about: meta.about || "",
            picture: meta.picture || ""
        };
    }

    static filterTagValue(event, tag) {
        return event.tags.filter(([t]) => t === tag);
    }

    static findTagValue(event, tag) {
        return event.tags.find(([t]) => t === tag)?.[1];
    }

    static findNip10MarkerValue(event, marker) {
        const eventTags = this.filterTagValue(event, "e");
        return eventTags.find(x => x[3] === marker)?.[1];
    }

    isSyntheticPrivateKey() {
        return this.privateKey === IsNip07 || this.privateKey === "none";
    }

    fetch(filters, quitMs = 0) {
        return this.backgroundNostr.fetch(filters, quitMs);
    }

    sub(filters, unsub = true, onEose = null) {
        return this.backgroundNostr.sub(filters, Comlink.proxy(event => {
            this.pushToEventBuffer(event);
        }), unsub, Comlink.proxy(() => {
            if (IsNotEmpty(onEose) && typeof onEose === "function") onEose();
        }));
    }

    unsub(subId) {
        return this.backgroundNostr.unsub(subId);
    }

    pushToEventBuffer(event) {
        const cacheKey = `${event.id}-emitted`;
        if (IsEmpty(this.nameCache[cacheKey])) {
            if (this.eventQueueFlag) {
                if (this.eventQueueBuffer.length > 0) {
                    this.eventQueue.push(...this.eventQueueBuffer);
                    this.eventQueueBuffer = [];
                }

                clearTimeout(this.eventQueueTimer);

                this.eventQueue.push(event);
                this.eventQueueTimer = setTimeout(() => {
                    this.processEventQueue().then();
                }, 50);
            } else {
                this.eventQueueBuffer.push(event);
            }

            this.nameCache[cacheKey] = 1;
        }
    }

    async processEventQueue() {
        this.eventQueueFlag = false;

        const profileUpdates = this.eventQueue.filter(event => event.kind === Kind.Metadata)
            .map(event => {
                const content = Nostr.parseJson(event.content);

                return IsNotEmpty(content) ? {
                    id: event.id,
                    creator: event.pubkey,
                    created: event.created_at,
                    nip05: event.nip05,
                    ...Nostr.normalizeMetadata(content)
                } : null;
            })
            .filter(IsNotEmpty);
        if (profileUpdates.length > 0) this.emit(NostrEventsEnum.ProfileUpdate, profileUpdates);

        const channelCreations = this.eventQueue.filter(event => event.kind === Kind.ChannelCreation)
            .map(event => {
                const content = Nostr.parseJson(event.content);

                return IsNotEmpty(content) ? {
                    id: event.id,
                    creator: event.pubkey,
                    created: event.created_at,
                    ...Nostr.normalizeMetadata(content)
                } : null;
            })
            .filter(IsNotEmpty);
        if (channelCreations.length > 0) this.emit(NostrEventsEnum.ChannelCreation, channelCreations);

        const channelUpdates = this.eventQueue.filter(event => event.kind === Kind.ChannelMetadata)
            .map(event => {
                const content = Nostr.parseJson(event.content);
                const channelId = Nostr.findTagValue(event, "e");

                if (IsEmpty(channelId)) return null;

                return IsNotEmpty(content) ? {
                    id: event.id,
                    creator: event.pubkey,
                    created: event.created_at,
                    channelId,
                    ...Nostr.normalizeMetadata(content)
                } : null;
            })
            .filter(IsNotEmpty);
        if (channelUpdates.length > 0) this.emit(NostrEventsEnum.ChannelUpdate, channelUpdates);

        const deletions = this.eventQueue.filter(event => event.kind === Kind.EventDeletion)
            .map(event => {
                const eventId = Nostr.findTagValue(event, "e");

                if (IsEmpty(eventId)) return null;

                return {
                    eventId,
                    reason: event.content || ""
                };
            })
            .filter(IsNotEmpty)
            .flat();
        if (deletions.length > 0) this.emit(NostrEventsEnum.EventDeletion, deletions);

        const publicMessages = this.eventQueue.filter(event => event.kind === Kind.ChannelMessage)
            .map(event => {
                const root = Nostr.findNip10MarkerValue(event, "root");
                const mentions = Nostr.filterTagValue(event, "p")
                    .map(x => x?.[1])
                    .filter(IsNotEmpty);

                if (IsEmpty(root)) return null;

                return IsNotEmpty(event.content) ? {
                    id: event.id,
                    content: event.content,
                    creator: event.pubkey,
                    created: event.created_at,
                    mentions: _.uniq(mentions),
                    root
                } : null;
            })
            .filter(IsNotEmpty);
        if (publicMessages.length > 0) this.emit(NostrEventsEnum.PublicMessage, publicMessages);

        const channelMessageHides = this.eventQueue.filter(event => event.kind === Kind.ChannelHideMessage)
            .map(event => {
                const content = Nostr.parseJson(event.content);
                const id = Nostr.findTagValue(event, "e");

                if (IsEmpty(id)) return null;

                return {
                    id,
                    reason: content?.reason || ""
                };
            })
            .filter(IsNotEmpty);
        if (channelMessageHides.length > 0) this.emit(NostrEventsEnum.ChannelMessageHide, channelMessageHides);

        const channelUserMutes = this.eventQueue.filter(event => event.kind === Kind.ChannelMuteUser)
            .map(event => {
                const content = Nostr.parseJson(event.content);
                const publicKey = Nostr.findTagValue(event, "p");

                if (IsEmpty(publicKey)) return null;

                return {
                    publicKey,
                    reason: content?.reason || ""
                };
            })
            .filter(IsNotEmpty);
        if (channelUserMutes.length > 0) this.emit(NostrEventsEnum.ChannelMessageHide, channelUserMutes);

        const reactions = this.eventQueue.filter(event => event.kind === Kind.Reaction)
            .map(event => {
                const message = Nostr.findNip10MarkerValue(event, "root");
                const peer = Nostr.findTagValue(event, "p");

                if (IsEmpty(message) || IsEmpty(peer) || IsEmpty(event.content)) return null;

                return {
                    id: event.id,
                    creator: event.pubkey,
                    created: event.created_at,
                    content: event.content,
                    message,
                    peer
                };
            })
            .filter(IsNotEmpty);
        if (reactions.length > 0) this.emit(NostrEventsEnum.Reaction, reactions);

        const muteListEvent = this.eventQueue.filter(event => event.kind === CustomKinds.MuteList)
            .sort((a, b) => b.created_at - a.created_at)[0];
        if (IsNotEmpty(muteListEvent)) {
            const visiblePublicKeys = Nostr.filterTagValue(muteListEvent, "p")
                .map(x => x?.[1]);

            if (IsNotEmpty(muteListEvent.content) && !this.isSyntheticPrivateKey()) {
                nip04.decrypt(this.privateKey, this.publicKey, muteListEvent.content)
                    .then(data => JSON.parse(data))
                    .then(value => {
                        const allPublicKey = [...visiblePublicKeys, ...value.map(x => x?.[1])];
                        this.emit(NostrEventsEnum.MuteList, {
                            publicKeys: allPublicKey,
                            encrypted: ""
                        });
                    });
            } else {
                this.emit(NostrEventsEnum.MuteList, {
                    publicKeys: visiblePublicKeys,
                    encrypted: muteListEvent.content.trim()
                });
            }
        }

        const leftChannelListEvent = this.eventQueue.filter(event =>
            event.kind === CustomKinds.Arbitrary &&
            Nostr.findTagValue(event, "d") === NostrTagsEnum.LeftChannelList
        )
            .sort((a, b) => b.created_at - a.created_at)[0];
        if (IsNotEmpty(leftChannelListEvent)) {
            const content = Nostr.parseJson(leftChannelListEvent.content);

            if (Array.isArray(content) && content.every(x => IsSha256(x))) this.emit(NostrEventsEnum.LeftChannelList, content);
        }

        const readMarkMapEvent = this.eventQueue.filter(event =>
            event.kind === CustomKinds.Arbitrary &&
            Nostr.findTagValue(event, "d") === NostrTagsEnum.ReadMarkMap
        )
            .sort((a, b) => b.created_at - a.created_at)[0];
        if (IsNotEmpty(readMarkMapEvent)) {
            const content = Nostr.parseJson(readMarkMapEvent.content);

            if (
                typeof content === "object" &&
                Object.keys(content).every(x => IsSha256(x))
            ) this.emit(NostrEventsEnum.ReadMarkMap, content);
        }

        const typings = this.eventQueue.filter(event =>
            event.kind === CustomKinds.Arbitrary &&
            Nostr.findTagValue(event, "d") === NostrTagsEnum.Typing
        )
            .map(event => {
                const peer = Nostr.findTagValue(event, "p");

                if (IsEmpty(peer) || IsEmpty(event.content)) return null;

                return {
                    id: event.id,
                    creator: event.pubkey,
                    created: event.created_at,
                    content: parseInt(event.content),
                    peer
                };
            })
            .filter(IsNotEmpty);
        if (typings.length > 0) this.emit(NostrEventsEnum.Typing, typings);

        Promise.all(
            this.eventQueue.filter(event => event.kind === Kind.EncryptedDirectMessage)
                .map(event => {
                    const receiver = Nostr.findTagValue(event, "p");

                    if (IsEmpty(receiver)) return null;

                    const root = Nostr.findNip10MarkerValue(event, "root");
                    const mentions = Nostr.filterTagValue(event, "p")
                        .map(c => c?.[1])
                        .filter(IsNotEmpty);
                    const peer = receiver === this.publicKey ? event.pubkey : receiver;

                    const message = {
                        id: event.id,
                        creator: event.pubkey,
                        created: event.created_at,
                        content: event.content,
                        mentions: _.uniq(mentions),
                        decrypted: false,
                        root,
                        peer
                    };

                    if (this.isSyntheticPrivateKey()) return message;

                    return nip04.decrypt(this.privateKey, peer, event.content)
                        .then(content => {
                            return {
                                ...message,
                                content,
                                decrypted: true
                            };
                        })
                        .catch(e => {
                            console.error("Failed to decrypt.", e);
                            return message;
                        });
                })
                .filter(IsNotEmpty)
        ).then(privateMessages => {
            if (privateMessages.length > 0) this.emit(NostrEventsEnum.DirectMessage, privateMessages);
        });

        this.eventQueue = [];
        this.eventQueueFlag = true;
    }

    fetchPreviousMessageChannel(channel, until) {
        return this.fetch([{
            kinds: [Kind.ChannelMessage],
            "#e": [channel],
            limit: MessagePerPage,
            until
        }], 1000).then(events => {
            events.forEach(event => this.pushToEventBuffer(event));

            return events.length;
        });
    }

    async fetchChannel(channelId) {
        const events = await this.fetch([
            {
                kinds: [Kind.ChannelCreation],
                ids: [channelId]
            },
            {
                kinds: [Kind.ChannelMetadata, Kind.EventDeletion],
                "#e": [channelId]
            }
        ]);
        if (IsEmpty(events)) return null;

        const creation = events.find(event => event.kind === Kind.ChannelCreation);
        if (IsEmpty(creation)) return null;

        if (
            events.find(event =>
                event.kind === Kind.EventDeletion &&
                event.pubkey === creation.pubkey
            )
        ) return null;

        const update = events.filter(event =>
            event.kind === Kind.ChannelMetadata &&
            event.pubkey === creation.pubkey
        )
            .sort((a, b) => b.created_at - a.created_at)[0];
        const content = Nostr.parseJson((update || creation).content);
        if (IsEmpty(content)) return null;

        return {
            id: creation.id,
            creator: creation.pubkey,
            created: creation.created_at,
            ...Nostr.normalizeMetadata(content)
        };
    }

    async fetchProfile(publicKey) {
        const event = (await this.fetch([{
            kinds: [Kind.Metadata],
            authors: [publicKey]
        }])).sort((a, b) => b.created_at - a.created_at)[0];
        if (IsEmpty(event)) return null;

        const content = Nostr.parseJson(event.content);
        if (IsEmpty(content)) return null;

        return {
            id: event.id,
            creator: event.pubkey,
            created: event.created_at,
            nip05: event.nip05,
            ...Nostr.normalizeMetadata(content)
        }
    }

    loadChannel(channelId) {
        this.sub([
            {
                kinds: [Kind.ChannelCreation],
                ids: [channelId]
            },
            {
                kinds: [Kind.ChannelMetadata, Kind.EventDeletion],
                "#e": [channelId]
            },
            {
                kinds: [Kind.ChannelMessage],
                limit: MessagePerPage,
                "#e": [channelId]
            }
        ]);
    }

    loadProfiles(publicKeys) {
        const authors = _.uniq(publicKeys)
            .filter(publicKey => IsEmpty(this.nameCache[publicKey]));
        if (IsEmpty(authors)) return null;

        authors.forEach(author => this.nameCache[author] = Date.now());

        _.chunk(authors, 20).forEach(author => {
            this.sub([{
                kinds: [Kind.Metadata],
                authors: author
            }]);
        });
    }

    listen(channels, since, onEose = null) {
        if (IsNotEmpty(this.listenerSub)) this.unsub(this.listenerSub);

        this.sub([
            {
                authors: [this.publicKey],
                since
            },
            {
                kinds: [Kind.EventDeletion, Kind.ChannelMetadata, Kind.ChannelMessage],
                "#e": channels,
                since
            },
            {
                kinds: [Kind.EncryptedDirectMessage],
                "#p": [this.publicKey],
                since
            },
            {
                kinds: [CustomKinds.Arbitrary],
                "#p": [this.publicKey],
                since
            }
        ], false, onEose).then(subId => {
            this.listenerSub = subId;
        });
    }

    listenMessages(messageIds, relIds, onEose = null) {
        if (IsNotEmpty(this.messageListenerSub)) this.unsub(this.messageListenerSub);

        this.sub([
            {
                kinds: [Kind.EventDeletion, Kind.ChannelMessage, Kind.Reaction],
                "#e": messageIds
            },
            ..._.chunk(relIds, 10).map(relId => ({
                kinds: [Kind.EventDeletion],
                "#e": relId
            }))
        ], false, onEose).then(subId => {
            this.messageListenerSub = subId;
        });
    }

    async updateProfile(profile) {
        const lastEvent = (await this.fetch([{
            kinds: [Kind.Metadata],
            authors: [this.publicKey]
        }])).sort((a, b) => b.created_at - a.created_at)[0];

        const content = IsNotEmpty(lastEvent?.content) ? Nostr.parseJson(lastEvent?.content) : "";
        const update = content.constructor === Object ? {
            ...content,
            ...profile
        } : {
            ...profile
        };

        return this.publish(Kind.Metadata, [], JSON.stringify(update));
    }

    async createChannel(meta) {
        return this.publish(Kind.ChannelCreation, [], JSON.stringify(meta));
    }

    async updateChannel(channel, meta) {
        const relay = await this.backgroundNostr.where(channel.id);

        return this.publish(Kind.ChannelMetadata, [["e", channel.id, relay]], JSON.stringify(meta));
    }

    async deleteEvents(eventIds, reason = "") {
        return this.publish(Kind.EventDeletion, [...eventIds.map(eventId => ["e", eventId])], reason);
    }

    async sendPublicMessage(channel, message, mentions, parent) {
        const root = parent || channel.id;
        const relay = await this.backgroundNostr.where(root);
        const tags = [["e", root, relay, "root"]];

        if (IsNotEmpty(mentions)) mentions.forEach(mention => tags.push(["p", mention]));

        return this.publish(Kind.ChannelMessage, tags, message);
    }

    async sendPrivateMessage(publicKey, message, mentions, parent) {
        const encrypted = await this.encrypt(publicKey, message);
        const tags = [["p", publicKey]];

        if (IsNotEmpty(mentions)) mentions.forEach(mention => tags.push(["p", mention]));

        if (IsNotEmpty(parent)) {
            const relay = await this.backgroundNostr.where(parent);
            tags.push(["e", parent, relay, "root"]);
        }

        return this.publish(Kind.EncryptedDirectMessage, tags, encrypted);
    }

    async sendPrivateGroupMessage(publicKeys, channel, message, mentions, parent) {
        let contents = {};
        for (const publicKey of publicKeys) {
            contents[publicKey] = await this.encrypt(publicKey, message);
        }
        const root = parent || channel.id;
        const relay = await this.backgroundNostr.where(root);
        const tags = [["e", root, relay, "root"]];

        if (IsNotEmpty(mentions)) mentions.forEach(mention => tags.push(["p", mention]));

        return this.publish(Kind.ChannelMessage, tags, contents);
    }

    async recommendRelay(relay) {
        return this.publish(Kind.RecommendRelay, [], relay);
    }

    async hideChannelMessage(channelId, reason) {
        return this.publish(Kind.ChannelHideMessage, [["e", channelId]], JSON.stringify({reason}));
    }

    async muteChannelUser(publicKey, reason) {
        return this.publish(Kind.ChannelMuteUser, [["p", publicKey]], JSON.stringify({reason}));
    }

    async updateMuteList(userIds) {
        const list = [...userIds.map(userId => ["p", userId])];
        const content = await this.encrypt(this.publicKey, JSON.stringify(list));

        return this.publish(CustomKinds.MuteList, [], content);
    }

    async sendReaction(message, publicKey, reaction) {
        const relay = await this.backgroundNostr.where(message);
        const tags = [
            ["e", message, relay, "root"],
            ["p", publicKey]
        ];

        return this.publish(Kind.Reaction, tags, reaction);
    }

    async updateLeftChannelList(channelIds) {
        const tags = [["d", NostrTagsEnum.LeftChannelList]];

        return this.publish(CustomKinds.Arbitrary, tags, JSON.stringify(channelIds));
    }

    async updateReadMarkMap(map) {
        const tags = [["d", NostrTagsEnum.ReadMarkMap]];

        return this.publish(CustomKinds.Arbitrary, tags, JSON.stringify(map));
    }

    async updateTyping(publicKey) {
        const tags = [
            ["d", NostrTagsEnum.Typing],
            ["p", publicKey]
        ];

        return this.publish(CustomKinds.Arbitrary, tags, UnixTime().toString());
    }

    publish(kind, tags, content) {
        return new Promise((resolve, reject) => {
            this.getSignature({
                kind,
                tags,
                pubkey: this.publicKey,
                content,
                created_at: UnixTime(),
                id: "",
                sig: ""
            }).then(sign => {
                this._publish(sign)
                    .then(event => resolve(event))
                    .catch(e => {
                        reject(e);
                    });
            }).catch((e) => {
                reject("Failed sign event.");
            });
        });
    }

    _publish(event) {
        return new Promise((resolve, reject) => {
            const pool = new SimplePool();

            if (IsEmpty(event)) {
                reject("Couldn't sign event.");
                return null;
            }

            if ([
                Kind.ChannelMessage,
                Kind.EncryptedDirectMessage
            ].includes(event.kind)) this.emit(NostrEventsEnum.Sending, event);

            this.pushToEventBuffer(event);

            let resolved = false;
            const okRelays = [];
            const failedRelays = [];
            const pub = pool.publish(this.writeRelays, event);

            const closePool = () => {
                if (
                    [
                        ...okRelays,
                        ...failedRelays
                    ].length === this.writeRelays.length
                ) pool.close(this.writeRelays);
            };

            const timeout = setTimeout(() => {
                pool.close(this.writeRelays);
                reject("Timeout.");
            }, 3000);

            pub.on("ok", relay => {
                okRelays.push(relay);
                if (!resolved) {
                    resolve(event);
                    resolved = true;
                    clearTimeout(timeout);
                }

                closePool();
            });

            pub.on("failed", relay => {
                failedRelays.push(relay);
                if (failedRelays.length === this.writeRelays.length) {
                    reject("Event couldn't be published on any relay.");
                    clearTimeout(timeout);
                }

                closePool();
            });
        });
    }

    async getSignature(event) {
        if (this.privateKey === IsNip07) {
            return window.nostr?.getSignature(event);
        } else {
            const privateKey = this.privateKey === "none" ? await window.requestPrivateKey(event) : this.privateKey;

            return {
                ...event,
                id: getEventHash(event),
                sig: getSignature(event, privateKey)
            };
        }
    }

    async encrypt(publicKey, content) {
        if (this.privateKey === IsNip07) {
            return window.nostr.nip04.encrypt(publicKey, content);
        } else {
            const privateKey = this.privateKey === "none" ? await window.requestPrivateKey({
                pubkey: publicKey,
                content
            }) : this.privateKey;

            return nip04.encrypt(privateKey, publicKey, content);
        }
    }

    close() {
        this.removeAllListeners();
        this.worker.terminate();
    }
};

export const initNostr = (keys) => {
    if (window.ntr) {
        window.ntr.close();
        window.ntr = undefined;
    }

    if (IsNotEmpty(keys)) window.ntr = new Nostr(keys?.privateKey.replace(/["']/g, ""), keys?.publicKey.replace(/["']/g, ""));

    return window.ntr;
};